import request from '@/utils/request'
// 获取物品详情
export function getProdetail (goodsId) {
  return request.get('/goods/detail', {
    params: {
      goodsId
    }
  })
}
// 获取评论
export function getDiscount (goodsId, limit) {
  return request.get('/comment/listRows', {
    params: {
      goodsId,
      limit
    }
  })
}

// 获取评论总数
export function getDiscountTotal (goodsId) {
  return request.get('/comment/total', {
    params: {
      goodsId
    }
  })
}
