<template>
  <div class="box">
    <!-- 页面标题 -->
    <NavBar>
      <template #title>商品详情页</template>
      <template #go> <van-icon name="arrow-left" /></template>
    </NavBar>
    <!-- 主体模块 -->
    <main>
     <div class="swiper" v-show="slideList.length !== 0">
       <!-- 图片展示(轮播图) -->
       <swiper :options="swiperOptions">
          <swiper-slide
            v-for="(item) in slideList"
            :key="item.external_url"
          >
          <img :src="item.external_url" alt="">
          </swiper-slide>
          <div
            class="swiper-pagination"
            slot="pagination"
          >
          </div>
        </swiper>
     </div>
     <!-- 产品 -->
     <div class="productDescribe" v-show="Object.keys(Prodetail).length !== 0">
        <div class="detail">
          <div class="price">
            <div>
              <span>￥{{Prodetail.goods_price_max}}</span>
              <del>￥{{Prodetail.line_price_max}}</del>
            </div>
            <div>已售{{Prodetail.goods_sales}}件</div>
          </div>
          <div class="protitle ellipsis">{{Prodetail.goods_name}}</div>
        </div>
        <!-- 货物运输 -->
        <div class="post">
          <div class="post-left">
            <span class="fontColorRed"><van-icon name="location-o" /></span>七天无理由退货
            <span class="fontColorRed"><van-icon name="location-o" /></span>48小时发货哦
          </div>
          <div class="post-right"><van-icon name="arrow" /></div>
        </div>
        <!-- 商品评价 -->
        <div class="assessment" >
          <div class="assessmentTitle">
            <span>商品评价（{{ total }}条）</span>
            <span>查看更多 <van-icon name="arrow" /></span>
          </div>
          <div class="thread" v-show="total">
            <ul>
              <li v-for="item in discountList" :key="item.comment_id">
                <div class="hd">
                  <img :src="item.user.avatar_url || defaultImage" alt="">
                  <h3>{{item.user.nick_name}}</h3>
                  <div><van-rate  color="#f0cf24" :value="item.score / 2" :size="num"/></div>
                </div>
                <div class="ma ellipsisOneRow">{{item.content}}</div>
                <div class="ft">{{ item.create_time }}</div>
              </li>
            </ul>
          </div>
        </div>
      </div>
      <div ref="discount" v-html="Prodetail.content"></div>
    </main>
    <!-- 底部模块 -->
    <footer>
      <div class="left">
        <div @click="$router.push('/home')">
          <span><van-icon name="wap-home-o"/></span>
          <span>首页</span>
        </div>
        <div  @click="$router.push('/cart')">
          <span>
            <van-icon v-if="cartList" name="shopping-cart-o" :badge="cartList"/>
            <van-icon v-else name="shopping-cart-o" />
          </span>
          <span>购物车</span>
        </div>
      </div>
      <div class="right">
        <button @click="addCartItems">加入购物车</button>
        <button  @click="adbuy">立即购买</button>
      </div>
    </footer>
    <!-- 反馈模块 -->
      <!-- 购物车 -->
    <van-action-sheet v-model="show" :title="btnBool ? '加入购物车': '立即购买'">
      <div class="content">
        <div class="main">
          <div class="image"><img :src="Prodetail.goods_image" alt=""></div>
          <div class="stock">
            <h1>￥<span>{{Prodetail.goods_price_max}}</span></h1>
            <span>库存{{Prodetail.stock_total}}</span>
          </div>
        </div>
        <div class="total">
          <span>数量</span>
          <numberBorderVue v-model.number="numberController"></numberBorderVue>
        </div>
        <div class="button" v-if="Prodetail.stock_total > 0">
          <button @click="adCart" v-if="btnBool">加入购物车</button>
          <button class="bgcOranged" v-else @click="buyNow">立即购买</button>
        </div>
        <div class="button" v-else>
          <button class="bgcGray" disabled>该商品已抢完</button>
        </div>
      </div>
    </van-action-sheet>
  </div>
</template>

<script>
import { getStorage } from '@/utils/storage'
import { swiper, swiperSlide } from 'vue-awesome-swiper'
import 'swiper/css/swiper.min.css'
import { getDiscount, getProdetail, getDiscountTotal } from '@/api/prodetail'
import { addCart } from '@/api/cart'
import defaultImage from '@/assets/defaultImage.webp'
import numberBorderVue from '@/components/numberBorder.vue'
import { Toast } from 'vant'
import loginConfirm from '@/mixins/loginConfirm'
export default {
  name: 'prodetailIndex',
  mixins: [loginConfirm],
  data () {
    return {
      num: ((16 / 3.75) * (document.documentElement.offsetWidth / 100)).toFixed(3),
      slideList: ['14pro.avif', '14pro.avif', '14pro.avif'],
      swiperOptions: {
        pagination: {
          el: '.swiper-pagination',
          type: 'fraction'
        }
      },
      Prodetail: {},
      discountList: [],
      total: 0,
      defaultImage: defaultImage,
      show: false,
      numberController: 1,
      btnBool: true
    }
  },
  components: {
    swiper,
    swiperSlide,
    numberBorderVue
  },
  updated () {
    // 每次请求成功后修改数据都会触发update，如果放在mounted里面只能监测到第一次，如果请求慢，数据更新不及时就没用
    this.$nextTick(() => {
      const tagObj = Array.from(this.$refs.discount.children)
      tagObj.forEach(item => {
        if (item.tagName === 'P') {
          Array.from(item.children).forEach(item => {
            item.style.width = '100%'
          })
        }
      })
    })
  },
  mounted () {
    window.addEventListener('resize', this.risize)
  },
  methods: {
    risize: function () {
      // 180和3.75是常量，变量是屏幕的相对值
      this.num = ((16 / 3.75) * (document.documentElement.offsetWidth / 100)).toFixed(3)
    },
    async Detail () {
      const { data: { detail } } = await getProdetail(this.$route.params.goodsId)
      this.Prodetail = detail
      this.slideList = detail.goods_images
    },
    async Discount () {
      const { data: { list } } = await getDiscount(this.$route.params.goodsId, 5)
      this.discountList = list
    },
    async DiscountTotal () {
      const { data: { total } } = await getDiscountTotal(this.$route.params.goodsId)
      this.total = total.all
    },
    async adCart () {
      this.btnBool = true
      // 判断是否登录
      if (!this.loginConfirm()) return
      const res = await addCart({
        goodsId: this.Prodetail.goods_id,
        goodsNum: this.numberController,
        goodsSkuId: this.Prodetail.skuList[0].goods_sku_id
      })
      Toast(res.message)
    },
    adbuy () {
      this.show = true
      this.btnBool = false
    },
    addCartItems () {
      this.show = true
      this.btnBool = true
    },
    async buyNow () {
      // 使用混入提供的方法来验证
      if (!this.loginConfirm()) return
      this.$router.push({
        path: '/pay',
        query: {
          mode: 'buyNow',
          goodsId: this.Prodetail.goods_id,
          goodsNum: this.numberController,
          goodsSkuId: this.Prodetail.skuList[0].goods_sku_id
        }
      })
    }
  },
  computed: {
    cartList () {
      if (!getStorage('check')) return false
      if (!getStorage('check').length) {
        return false
      } else {
        return this.$store.state.cart.cartList.length || getStorage('check').length
      }
    }
  },
  created () {
    this.Detail()
    this.Discount()
    this.DiscountTotal()
  },
  beforeDestroy () {
    // 销毁组件之前，清理事件
    window.removeEventListener('resize', this.risize)
  }
}
</script>

<style lang="less" scoped>
// 反馈组件
.content {
  height: 200px;
  padding: 0 10px;
  display: flex;
  flex-direction: column;
  .main {
    display: flex;
    .image {
      margin-right: 20px;
      width: 100px;
      height: 100px;
      img {
        width: 100%;
        height: 100;
      }
    }
    .stock {
      margin-top: 2px;
      h1 {
        color: orangered;
        font-size: 14px;
        span {
          font-size: 26px;
          font-weight: 400;
          margin-left: 2px;
          color: orangered;
        }
      }
      span {
        font-size: 16px;
        color: gray;
      }
    }
  }
  .total {
    display: flex;
    height: 50px;
    justify-content: space-between;
    align-items: center;
    font-size: 16px;
  }
  .button {
    flex: 1;
    padding: 0 10px;
    display: flex;
    align-items: center;
    button {
      width: 100%;
      height: 40px;
      border: 0;
      border-radius: 31px;
      background-color: #ff9601;
      font-size: 16px;
      color: #fff;
    }
  }

}
.fontColorRed {
  color: red;
}
.bgcGray {
  background-color: gray !important;
}
.bgcOranged {
  background-color: orangered !important;
}
// 底部
footer {
background-color: #fff;
position: fixed;
bottom: 0;
left: 0;
right: 0;
height: 50px;
display: flex;
border-top: 1px solid gray;
.left {
  width: 30%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  span {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 12px;
    i {
      font-size:20px;
    }
  }
}
.right {
  flex: 1;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding:0 3px;
  button {
    width: 47%;
    height: 70%;
    border: 0;
    border-radius: 17.5px;
    font-size: 14px;
    color: #fff;
    &:nth-child(1) {
      background-color: #fda900;
    }
    &:nth-child(2) {
      background-color: #ff6526;
    }
  }
}
}
// 主体部分
main {
background-color: #fff;

  position: fixed;
  top: 50px;
  bottom: 50px;
  left: 0;
  right: 0;
  overflow-y: auto;
  overflow-x: hidden;
  .swiper {
    height: 482px;
    .swiper-container  {
      height: 100%;
     .swiper-slide {
      display: flex;
      justify-content: center;
      align-items: center;
      img {
        width: 90%;
      }
     }
    }
    .swiper-pagination  {
      width: 40px;
      border-radius: 18.667px;
      margin-left: 84%;
      margin-bottom: 20px;
      padding: 2px 5px;
      background: lightgray;
      box-sizing: content-box;
      font-size: 16px;
    }
  }
  // 产品
  .productDescribe {
    padding: 0 10px;
    .detail {
      .price {
        height: 40px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        div {
          &:nth-child(1) {
            span {
              font-size: 18px;
              color: red;
              margin-right: 5px;
            }
            del {
              font-size: 14px;
              color: gray;
            }
          }
          &:nth-child(2) {
            font-size: 15px;
            color: gray;
          }
        }
      }
      .protitle {
        font-size: 16px;
      }
    }
  }
  // 发货
  .post {
    height: 50px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 16px;
    span {
      margin-right: 5px;
    }
  }
  .assessment {
    margin-bottom: 30px;
    .assessmentTitle {
      display: flex;
      justify-content: space-between;
      align-items: center;
      span {
        &:nth-child(1) {
          font-size: 16px;
        }
        &:nth-child(2) {
          font-size: 14px;
          color: gray;
        }
      }
    }
    .thread {
      ul {
        li {
          .hd {
            margin-top: 23px;
            display: flex;
            align-items: center;
            img {
              width: 20px;
              margin: 0px 10px 0 0;
            }
            h3 {
              font-weight: 400;
              font-size: 16px;
              margin-right: 10px;
            }
          }
        }
      }
    }
    .ma {
      margin-top: 7px;
      font-size: 16px;
    }
    .ft {
      font-size: 14px;
      color: gray;
      margin-top: 10px;
    }
  }

}
</style>
